html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

body {
  padding: 0 0 0 0;
  font-family: sans-serif;
  display: flex;
  color: #f5f5f5;
  background-image: linear-gradient(-45deg,#000000,#333333);
}
